const WebFormList = ({ webFields, removeFromList }) => {

  const list = webFields.map(({ _id, fieldName, fieldInForm, fieldType }) =>
    fieldInForm && fieldType !== "select" ? <li key={_id}>{fieldName}<button id={fieldName} onClick={removeFromList}>X</button></li> : null
  );

  return <ul>{list}</ul>;
};

export default WebFormList;
